"use client";

import { useState } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { Footer } from "@/components/footer";
import {
  FiChevronDown,
  FiArrowLeft,
  FiLock,
  FiDatabase,
  FiUser,
  FiShield,
  FiShare2,
  FiEdit3,
} from "react-icons/fi";
import Link from "next/link";

export default function PrivacyPage() {
  const [openIndex, setOpenIndex] = useState<number | null>(null);

  const items = [
    {
      icon: <FiUser className="text-purple-400 text-xl" />,
      title:
        "Данные для регистрации и идентификации: Имя, фамилия, email, номер телефона, платежные реквизиты.",
      details: (
        <>
          <p>
            Эти данные используются для создания вашего аккаунта, авторизации,
            а также для связи с вами при необходимости и обработки платежей. Мы
            не передаем их третьим лицам без вашего согласия.
          </p>
        </>
      ),
    },
    {
      icon: <FiDatabase className="text-purple-400 text-xl" />,
      title:
        "Технические данные: IP-адреса, логи доступа, cookies и данные об использовании услуг.",
      details: (
        <>
          <p>
            Эти сведения помогают анализировать работу сайта, улучшать
            производительность и предотвращать возможные злоупотребления. Cookies
            применяются для сохранения настроек и аналитики.
          </p>
        </>
      ),
    },
    {
      icon: <FiLock className="text-purple-400 text-xl" />,
      title: "Данные, которые вы храните на сервере.",
      details: (
        <>
          <p>
            Мы не просматриваем и не анализируем ваши данные, кроме случаев,
            предусмотренных законом или при нарушении условий использования.
            Конфиденциальность полностью сохраняется.
          </p>
        </>
      ),
    },
    {
      icon: <FiShare2 className="text-purple-400 text-xl" />,
      title: "Передача данных третьим лицам.",
      details: (
        <>
          <p>
            Мы не продаем персональные данные. Передача возможна только надежным
            партнерам (платежные шлюзы, email-сервисы) и только для выполнения
            обязательств перед вами.
          </p>
        </>
      ),
    },
    {
      icon: <FiShield className="text-purple-400 text-xl" />,
      title: "Хранение и защита данных.",
      details: (
        <>
          <p>
            Мы используем современные технологии шифрования и защиты информации.
            Доступ к серверам имеет строго ограниченный круг специалистов.
          </p>
        </>
      ),
    },
    {
      icon: <FiEdit3 className="text-purple-400 text-xl" />,
      title: "Ваши права.",
      details: (
        <>
          <p>
            Вы можете запросить удаление, изменение или просмотр своих данных, а
            также отказаться от маркетинговых рассылок в любой момент.
          </p>
        </>
      ),
    },
  ];

  return (
    <div className="min-h-screen gradient-purple text-white py-16 px-6">
      <div className="max-w-3xl mx-auto space-y-10">
        {/* Кнопка "Назад" */}
        <div>
          <Link
            href="/"
            className="inline-flex items-center gap-2 text-purple-300 hover:text-purple-400 transition-colors duration-200 font-medium"
          >
            <FiArrowLeft className="text-lg" />
            Назад на главную
          </Link>
        </div>

        {/* Заголовок */}
        <h1 className="text-4xl font-bold text-center mb-6">
          Политика конфиденциальности
        </h1>

        {/* Контент */}
        <div className="space-y-4">
          {items.map((item, index) => (
            <motion.div
              key={index}
              initial={{ opacity: 0, y: 15 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.3, delay: index * 0.05 }}
              className="bg-white/10 backdrop-blur-xl border border-white/20 rounded-2xl overflow-hidden hover:shadow-lg hover:shadow-purple-500/10 transition-all"
            >
              <button
                onClick={() =>
                  setOpenIndex(openIndex === index ? null : index)
                }
                className="w-full text-left px-6 py-5 flex justify-between items-center font-medium text-lg hover:bg-white/10 transition"
              >
                <div className="flex items-center gap-3">
                  {item.icon}
                  <span>{item.title}</span>
                </div>

                <motion.span
                  animate={{ rotate: openIndex === index ? 180 : 0 }}
                  transition={{ duration: 0.3 }}
                >
                  <FiChevronDown className="text-purple-400 text-xl" />
                </motion.span>
              </button>

              <AnimatePresence initial={false}>
                {openIndex === index && (
                  <motion.div
                    key="content"
                    initial={{ opacity: 0, height: 0 }}
                    animate={{ opacity: 1, height: "auto" }}
                    exit={{ opacity: 0, height: 0 }}
                    transition={{ duration: 0.4, ease: "easeInOut" }}
                    className="px-6 pb-5 text-gray-200 text-base leading-relaxed"
                  >
                    <div className="mt-2 space-y-2">{item.details}</div>
                  </motion.div>
                )}
              </AnimatePresence>
            </motion.div>
          ))}
        </div>

        <div className="mt-16">
          <Footer />
        </div>
      </div>
    </div>
  );
}
